/*
 * Стили компонента SearchForm
 */





.SearchForm-button-label {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  clip-path: inset(50%);
  border: 0;
}



.SearchForm-input::placeholder {
  font: inherit;
  font-size: 18px;
}

